<template>
  <el-card style="margin: 15px;min-height:calc(100vh - 80px)">
    <!--    头部数据-->
    <div>
      <el-row :gutter="20" class="topInfo">
        <el-col :span="6">
          <div id="stuNumDiv" class="el-colDiv">
            <div id="ssv1-main-text" class="nowDiv">实时</div>
            <span class="title">学生统计</span><br>
            <span class="digital">{{ this.studentNum }}</span><br>
            <span class="last-span">当前分类总记录数</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div id="haveRoomDiv" class="el-colDiv">
            <div id="ssv2-main-text" class="nowDiv">实时</div>
            <span class="title">住宿人数</span><br>
            <span class="digital">{{ this.haveRoomStudentNum }}</span><br>
            <span class="last-span">当前分类总记录数</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div id="repairNum" class="el-colDiv">
            <div id="ssv3-main-text" class="nowDiv">实时</div>
            <span class="title">报修统计</span><br>
            <span class="digital">{{ this.repairOrderNum }}</span><br>
            <span class="last-span">当前分类总记录数</span>
          </div>
        </el-col>
        <el-col :span="6">
          <div id="emptyRoom" class="el-colDiv">
            <div id="ssv4-main-text" class="nowDiv">实时</div>
            <span class="title">空宿舍统计</span><br>
            <span class="digital">{{ this.noFullRoomNum }}</span><br>
            <span class="last-span">当前分类总记录数</span>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 下部-->
    <div style="display: flex;width: 100%;margin-top: 40px;align-items: center;justify-content: center;">

      <!--   左侧 宿舍通告-->
      <div style="margin-right: 5%;min-height:380px;padding:20px;max-height:440px;width:50%;border-radius: 4px;box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)">
        <span style="font-size: 22px;display:block;margin-bottom: 30px;margin-left: 10px">宿舍通告</span>
        <el-timeline>
          <el-timeline-item
              v-for="(activity, index) in activities.slice(0,8)"
              :key="index"
              :timestamp="activity.releaseTime"
          >
            <span style="font-size: 15px">{{ activity.title }}</span>
          </el-timeline-item>
        </el-timeline>
      </div>
      <!--   中部-->
<!--      <div style="height: 588px">-->
<!--        <span style="font-size: 22px;display:block;margin-bottom: 30px;margin-left: 10px">宿舍学生人数分布图</span>-->
<!--        <home_echarts/>-->
<!--      </div>-->
      <!--  右侧-->
      <div style="margin-left: 5%">
        <!--   天气组件-->
<!--        <weather/>-->
        <!--    日历组件-->
        <el-card style="width:380px;max-height:440px;margin-top: 17px">
          <Calender/>
        </el-card>
      </div>
    </div>
  </el-card>
</template>

<script>
import Calender from "@/components/Calendar"
import request from "@/utils/request";
const {ElMessage} = require("element-plus");

export default {
  name: "Home",
  components: {
    Calender,
  },
  data() {
    return {
      studentNum: '',
      haveRoomStudentNum: '',
      repairOrderNum: '',
      noFullRoomNum: '',
      activities: [],
    }
  },
  created() {
    this.getHomePageNotice()
    this.getStuNum()
    this.getHaveRoomNum()
    this.getOrderNum()
    this.getNoFullRoom()
  },
  methods: {
    async getStuNum() {
      request.get("/stu/stuNum").then(res => {
        if (res.code === '0') {
          this.studentNum = res.data
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    },
    async getHaveRoomNum() {
      request.get("/stu/haveRoomNum").then(res => {
        if (res.code === '0') {
          this.haveRoomStudentNum = res.data
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    },
    async getOrderNum() {
      request.get("/repair/orderNum").then(res => {
        if (res.code === '0') {
          this.repairOrderNum = res.data
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    },
    async getNoFullRoom() {
      request.get("/room/noFullRoom").then(res => {
        if (res.code === '0') {
          this.noFullRoomNum = res.data
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    },
    async getHomePageNotice() {
      request.get("/notice/homePageNotice").then(res => {
        if (res.code === '0') {
          this.activities = res.data
        } else {
          ElMessage({
            message: res.msg,
            type: 'error',
          })
        }
      })
    },
  },
}
</script>

<style scoped>
.wv-lt-refresh {
  display: none;
}

.topInfo {
  margin: 0 auto;
}

.el-colDiv {
  margin: 20px auto;
  max-width: 340px;
  min-width: 200px;
  overflow: hidden;
  height: 115px;
  border-radius: 5px;
  background-color: black;
  color: white;
  padding-left: 15px;
  padding-top: 15px;
  position: relative;
}

.nowDiv {
  width: 38px;
  height: 19px;
  position: absolute;
  right: 5%;
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
}

.digital {
  font-size: 25px;
  line-height: normal;
  margin-left: 2px;
}

.title {
  font-size: 18px;
}

.last-span {
  font-size: 13px;
}

#stuNumDiv {
  background-color: #2e4057;
}

#ssv1-main-text {
  background-color: #1398ff;
}

#haveRoomDiv {
  background-color: #1398ff;
}

#ssv2-main-text {
  background-color: #2e4057;
}

#repairNum {
  background-color: #008789;
}

#ssv3-main-text {
  background-color: #ffb400;
}

#emptyRoom {
  background-color: #ffb400;
}

#ssv4-main-text {
  background-color: #008789;
}
</style>